:root {
  --bg-color-main: #ffffff;
  --bg-color-primery: #f5f4fa;
  --bg-color-secondary: #2f303a;
  --bg-color-accent: #2196f3;
  --txt-color-main: #212121;
  --txt-color-primery: #757575;
  --txt-color-secondary: #ffffff;
  --txt-color-accent: #2196f3;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: var(--bg-color-main);
  color: var(--txt-color-main);
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}

h1,
h2,
h3,
h4,
p {
  margin: 0px;
}

img {
  display: block;
}

.container {
  width: 1200px;
  padding: 0px 15px;
  margin: 0 auto;
}
.container--center {
  display: flex;
  align-items: center;
}
.container--baseline {
  display: flex;
  align-items: baseline;
}

.section {
  padding-top: 94px;
  padding-bottom: 94px;
}
.section--dark-bg {
  background-color: var(--bg-color-primery);
}
.section--without-pb {
  padding-bottom: 0px;
}

.section__title {
  margin-bottom: 50px;
  font-weight: 700;
  font-size: 36px;
  line-height: 1.17;
  text-align: center;
  letter-spacing: 0.03em;
}

.button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  min-width: 200px;
  padding: 9px 28px;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.88;
  letter-spacing: 0.06em;
  background-color: var(--bg-color-accent);
  color: var(--txt-color-secondary);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-color: transparent;
  border-radius: 4px;
  cursor: pointer;
}

.filter {
  display: flex;
  justify-content: center;
  margin-bottom: 50px;
}

.filter__button {
  min-width: 73px;
  padding: 6px 22px;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.6;
  text-align: center;
  letter-spacing: 0.03em;
  background-color: var(--bg-color-primery);
  color: var(--txt-color-main);
  border-radius: 4px;
  border: transparent;
  outline: none;
  transition-property: all;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.filter__button:not(:last-child) {
  margin-right: 8px;
}
.filter__button:hover, .filter__button:focus {
  background-color: var(--bg-color-accent);
  color: var(--txt-color-secondary);
  box-shadow: 0px 3px 1px rgba(0, 0, 0, 0.1), 0px 1px 2px rgba(0, 0, 0, 0.08), 0px 2px 2px rgba(0, 0, 0, 0.12);
}

.socials {
  display: flex;
  justify-content: space-between;
}

.socials__link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  color: #afb1b8;
  transition-property: all;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.socials__link:hover, .socials__link:focus {
  color: var(--txt-color-secondary);
  background-color: var(--bg-color-accent);
}

.socials__link--dark-bg {
  background-color: rgba(255, 255, 255, 0.1);
  color: var(--txt-color-secondary);
  transition-property: all;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.socials__link--dark-bg:hover, .socials__link--dark-bg:focus {
  background-color: var(--bg-color-accent);
}

.socials__icon {
  fill: currentColor;
}

.page-header {
  border-bottom: 1px solid #ececec;
}

.page-header__logo {
  margin-right: 94px;
  color: var(--txt-color-main);
}

.logo {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  font-size: 26px;
  line-height: 1.19;
  letter-spacing: 0.03em;
  padding: 24px 0;
}
.logo__accent {
  color: var(--txt-color-accent);
}

.site-nav {
  display: flex;
  align-items: center;
  margin-right: auto;
}

.site-nav__list {
  display: flex;
}

.site-nav__item:not(:last-child) {
  margin-right: 50px;
}

.site-nav__link {
  position: relative;
  display: flex;
  align-items: center;
  padding: 31px 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.02em;
  color: var(--txt-color-main);
  transition-property: color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.site-nav__link--current {
  color: var(--txt-color-accent);
}
.site-nav__link--current::after {
  position: absolute;
  content: "";
  top: 98%;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: var(--bg-color-accent);
  border-radius: 2px;
}
.site-nav__link:hover, .site-nav__link:focus {
  color: var(--txt-color-accent);
}

.header-contacts__link {
  position: relative;
  display: flex;
  align-items: center;
  padding: 31px 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.02em;
  color: var(--txt-color-main);
  transition-property: all;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.header-contacts__link:hover, .header-contacts__link:focus {
  color: var(--txt-color-accent);
}
.header-contacts__link:first-child {
  margin-right: 50px;
}

.header-contacts {
  display: inline-flex;
  font-style: normal;
  color: var(--txt-color-primery);
}

.header-contacts__icon {
  width: 18px;
  height: 18px;
  margin-right: 10px;
  fill: currentColor;
}

.portfolio-list {
  display: flex;
  flex-wrap: wrap;
  margin-right: -30px;
  margin-bottom: -30px;
}

.portfolio-list__item {
  flex-basis: calc(100% / 3 - 30px);
  margin-right: 30px;
  margin-bottom: 30px;
}

.portfolio-list__link {
  display: inline-block;
  transition-property: box-shadow;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.portfolio-list__link:hover, .portfolio-list__link:focus {
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16);
}

.portfolio-list__content {
  /* width: 370px; */
  padding: 20px 24px;
  border-right: 1px solid #eeeeee;
  border-left: 1px solid #eeeeee;
  border-bottom: 1px solid #eeeeee;
}

.portfolio-list__thumb {
  position: relative;
  overflow: hidden;
}

.portfolio-list__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 63px 24px;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.56;
  letter-spacing: 0.03em;
  background: rgba(33, 150, 243, 0.9);
  color: var(--txt-color-secondary);
  transform: translateY(101%);
  transition-property: transform;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.portfolio-list__link:hover .portfolio-list__overlay, .portfolio-list__link:focus .portfolio-list__overlay {
  transform: translateY(0);
}

.portfolio-list__title {
  margin-bottom: 4px;
  font-weight: 700;
  font-size: 18px;
  line-height: 2;
  letter-spacing: 0.06em;
  color: var(--txt-color-main);
}

.portfolio-list__desc {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.8;
  letter-spacing: 0.03em;
  color: var(--txt-color-primery);
}

.page-footer {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: var(--bg-color-secondary);
}

.page-footer__logo {
  display: inline-block;
  padding: 0;
  margin-bottom: 20px;
  color: var(--txt-color-secondary);
}

.contacts {
  margin-right: 70px;
}

.footer-contacts {
  display: flex;
  flex-direction: column;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.71;
  letter-spacing: 0.03em;
  color: var(--txt-color-secondary);
}

.footer-contacts__email,
.footer-contacts__phone {
  color: var(--txt-color-primery);
}

.footer-contacts__address,
.footer-contacts__email {
  margin-bottom: 9px;
}

.footer-socials {
  width: 206px;
}

.footer-socials__text {
  margin-bottom: 20px;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: var(--txt-color-secondary);
}

.subscribe {
  margin-left: auto;
}

.subscribe__text {
  margin-bottom: 20px;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: var(--txt-color-secondary);
}

.subscribe__input {
  width: 358px;
  padding: 15px;
  margin-right: 12px;
  font-size: 16px;
  line-height: 1.25;
  letter-spacing: 0.03em;
  color: rgba(255, 255, 255, 0.6);
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.3);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));
  border-radius: 4px;
  transition-property: border-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.subscribe__input:hover, .subscribe__input:focus {
  outline: none;
  border-color: var(--bg-color-accent);
}
.subscribe__input::placeholder {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;
  letter-spacing: 0.03em;
  color: rgba(255, 255, 255, 0.6);
}

.subscribe__button {
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
}
.subscribe__button::after {
  content: " ";
  display: inline-block;
  width: 24px;
  height: 24px;
  background-image: url("../images/icon-send.svg");
}

